import React, {useEffect, useState,useRef} from "react";  /* 案例一  */
import {List, Switch ,Button} from 'antd-mobile';

export default function Index(props) {
    const {match: {params}} = props;
    const [checked, setChecked] = useState(true);
    let CompClass1Ref = useRef(null);

    const commitFn = () =>{
        console.log("CompClass1",CompClass1Ref.current());
        console.log("CompClass2",checked);
    }

    return <div>
            产品 {params.id} page!!
            <CompClass1 defaultVal={true} CompClass1Ref={CompClass1Ref}/>
            <CompClass2 checked={checked} setChecked={setChecked}/>
        <Button type={"primary"} onClick={commitFn}>提交</Button>
    </div>
}


const CompClass1 = ({defaultVal,CompClass1Ref}) =>{
    /* 这个默认值是对界面的交互优化，防止二次渲染，闪屏，如果当组件首次加载时，会执行这里的默认值，然后再走didmount */
    const [checked, setChecked] = useState(defaultVal);
    const upData = (data)=>{
        setChecked(data.checked);
        CompClass1Ref.current = () =>{
            return { checked:data.checked }
        }
    }

    useEffect(()=>{
        upData({checked:defaultVal})
    },[])

    return  <List
        renderHeader={() => '内控状态组件'}
    >
        <List.Item
            extra={<Switch
                checked={checked}
                onChange={() => {
                    upData({checked:!checked})
                }}
            />}
        >Off</List.Item>
    </List>
}

const CompClass2 = ({checked,setChecked}) =>{

    return  <List
        renderHeader={() => '外控状态组件'}
    >
        <List.Item
            extra={<Switch
                checked={checked}
                onChange={() => {
                    setChecked(!checked)
                }}
            />}
        >Off</List.Item>
    </List>
}